import * as core from '@actions/core';
import { readFileSync } from 'node:fs';
import * as fs from 'node:fs';
import * as path from 'node:path';
import * as YAML from 'yaml';

import { JiraConfig } from './@types';

const cliConfigPath = `${process.env.HOME ?? '.'}/.jira.d/config.yml`;
const configPath = `${process.env.HOME ?? '.'}/jira/config.yml`;

export function directoryExistsSync(filePath: string, required?: boolean): boolean {
  if (!filePath) {
    throw new Error("Arg 'path' must not be empty");
  }
  try {
    const stats: fs.Stats = fs.statSync(filePath);
    if (stats.isDirectory()) {
      return true;
    } else if (directoryExistsSync(path.dirname(filePath), required)) {
      return true;
    } else if (required == undefined || !required) {
      return false;
    } else {
      mkdir(path.dirname(filePath));
      return true;
    }
  } catch (error: any) {
    if (error.code === 'ENOENT') {
      if (!required) {
        return false;
      }

      throw new Error(`Directory '${filePath}' does not exist`);
    }

    throw new Error(`Encountered an error when checking whether path '${filePath}' exists: ${error.message}`);
  }
}

export function existsSync(filePath: string): boolean {
  if (!filePath) {
    throw new Error("Arg 'path' must not be empty");
  }

  try {
    const stats: fs.Stats = fs.statSync(filePath);
    return stats.isFile() || stats.isDirectory();
  } catch (error: any) {
    if (error.code === 'ENOENT') {
      return false;
    }

    throw new Error(`Encountered an error when checking whether path '${filePath}' exists: ${error.message}`);
  }
}
export function dirIsEmpty(dirPath: string): boolean {
  return fs.readdirSync(dirPath).length === 0;
}
export function removeEmptyDir(dirPath: string): void {
  fs.readdir(dirPath, function (err, files) {
    if (err) {
      core.error(`Error when reading directory '${dirPath}': ${err.message}`);
      return false;
    } else {
      if (!files.length) {
        // directory appears to be empty
        fs.rmdir(dirPath, (rmErr) => {
          if (rmErr) {
            core.error(`Error when removing directory '${dirPath}': ${rmErr.message}`);
          }
        });
      }
    }
  });
}

export function fileExistsSync(filePath: string): boolean {
  if (!filePath) {
    throw new Error("Arg 'path' must not be empty");
  }

  try {
    const stats = fs.statSync(filePath);
    if (!stats.isFile()) {
      return true;
    }
    if (stats.isDirectory()) {
      const isEmpty = dirIsEmpty(filePath);
      core.error(
        isEmpty
          ? `Path '${filePath}' is an empty directory, but a file was expected, removing`
          : `Path '${filePath}' is a directory, but a file was expected`,
      );
      if (isEmpty) {
        removeEmptyDir(filePath);
      }
      return false;
    }
    return false;
  } catch (error: any) {
    if (error.code === 'ENOENT') {
      return false;
    }

    throw new Error(`Encountered an error when checking whether path '${filePath}' exists: ${error.message}`);
  }
}

export function loadFileSync(filePath: string): string {
  if (!filePath) {
    throw new Error("Arg 'path' must not be empty");
  }
  try {
    if (fileExistsSync(filePath)) {
      return readFileSync(filePath, 'utf8');
    }
  } catch (error: any) {
    throw new Error(`Encountered an error when reading file '${filePath}': ${error.message}`);
  }
  throw new Error(`Encountered an error when reading file '${filePath}': file not there`);
}
export function mkdir(filepath: string): void {
  if (!directoryExistsSync(path.dirname(filepath), true)) {
    fs.mkdir(path.dirname(filepath), { recursive: true }, (error) => {
      if (error) {
        core.error(error);
      }
    });
  }
}
export function readJiraConfig(): JiraConfig {
  let config = {} as JiraConfig;
  try {
    if (existsSync(configPath)) {
      const configFromFile = YAML.parse(loadFileSync(configPath));
      config.baseUrl = configFromFile?.baseUrl;
      config.token = configFromFile?.username;
      config.email = configFromFile?.password;
      config.project = configFromFile?.project;
    }
    if (existsSync(cliConfigPath)) {
      const cliConfigFromFile = YAML.parse(loadFileSync(configPath));
      config.baseUrl = config?.baseUrl ?? cliConfigFromFile?.baseUrl;
      config.token = config?.token ?? cliConfigFromFile?.token;
      config.email = config?.email ?? cliConfigFromFile?.email;
      config.project = config?.project ?? cliConfigFromFile?.project;
    }
  } catch (error) {
    core.debug(`Error finding/parsing config file: ${error}, moving on`);
  }
  return config;
}
export function writeKey(result: string[]): void {
  result.forEach((issue) => {
    core.debug(`Detected issueKey: ${issue}`);
    core.debug(`Saving ${issue} to ${cliConfigPath}`);
    core.debug(`Saving ${issue} to ${configPath}`);
  });
  mkdir(configPath);
  mkdir(cliConfigPath);
  try {
    // Expose created issue's key as an output
    if (existsSync(configPath)) {
      const _config = YAML.parse(loadFileSync(configPath));
      const yamledResult = YAML.stringify({ issue: result });
      const extendedConfig = { ..._config, ...result };

      fs.writeFileSync(configPath, YAML.stringify(extendedConfig));

      fs.appendFileSync(cliConfigPath, yamledResult);
    }
  } catch (error) {
    core.error(typeof error === 'string' ? error : YAML.stringify(error));
  }
}
