import * as core from '@actions/core';
import * as path from 'path';

import { Args, JiraAuthConfig } from './@types';
import * as fsHelper from './fs-helper';
import { undefinedOnEmpty } from './utils';

export function getInputs(): Args {
  const obj = {} as unknown;
  const result = obj as Args;
  const jiraConfigBase: JiraAuthConfig = fsHelper.readJiraConfig();
  const jiraConfig: JiraAuthConfig = {
    baseUrl: process.env.JIRA_BASE_URL || undefinedOnEmpty(core.getInput('jira_base_url')) || jiraConfigBase?.baseUrl,
    token: process.env.JIRA_API_TOKEN || undefinedOnEmpty(core.getInput('jira_api_token')) || jiraConfigBase?.token,
    email: process.env.JIRA_USER_EMAIL || undefinedOnEmpty(core.getInput('jira_user_email')) || jiraConfigBase?.email,
  };

  if (!jiraConfig.email || !jiraConfig.token || !jiraConfig.baseUrl) {
    let errMsg = '';
    errMsg += `JIRA_BASE_URL was ${!jiraConfig.baseUrl ? 'missing' : 'found'}, `;
    errMsg += `JIRA_API_TOKEN was ${!jiraConfig.token ? 'missing' : 'found'}, `;
    errMsg += `and JIRA_USER_EMAIL ${!jiraConfig.email ? 'missing' : 'found'}, `;
    errMsg += `but all are required`;
    throw new Error(errMsg);
  }

  result.config = jiraConfig;
  result.githubApiBaseUrl = core.getInput('github-api-url') || process.env.GITHUB_API_URL || undefined;
  result.token = core.getInput('token') || core.getInput('github-token') || process.env.GITHUB_TOKEN || 'NO_TOKEN';
  result.string = core.getInput('string');
  result.baseRef = core.getInput('base_ref');
  result.headRef = core.getInput('head_ref');
  result.projects = core.getInput('projects');
  result.projectsIgnore = core.getInput('projects_ignore');
  result.includeMergeMessages = core.getBooleanInput('include_merge_messages');
  result.ignoreCommits = core.getBooleanInput('ignore_commits');
  result.failOnError = core.getBooleanInput('fail_on_error');

  // GitHub workspace
  let githubWorkspacePath = process.env.GITHUB_WORKSPACE;
  if (!githubWorkspacePath) {
    throw new Error('GITHUB_WORKSPACE not defined');
  }
  githubWorkspacePath = path.resolve(githubWorkspacePath);
  core.debug(`GITHUB_WORKSPACE = '${githubWorkspacePath}'`);
  fsHelper.directoryExistsSync(githubWorkspacePath, true);

  return result;
}
